import "../App.css";
import { Link } from "react-router-dom";
function Navbar() {
  return (
    <nav className="navbar navbar-expand-lg ps-5">
      <div className="container-fluid">
        <Link className="navbar-brand" href="#">
          <img
            src={
              "data:image/png;base64,"
            }
            className="Logo"
          />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavDropdown"
          aria-controls="navbarNavDropdown"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavDropdown">
          <ul className="navbar-nav justify-content-space-around ps-5 pt-4">
            <li className="nav-item">
              <Link
                className="nav-link pe-5 ps-4"
                aria-current="page"
                to="/features"
              >
                Features
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link pe-5" to="/business">
                Business
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link pe-5" to="/company">
                Company
              </Link>
            </li>
            <li>
              <Link className="nav-link pe-5" to="/help">
                Help
              </Link>
            </li>
            <div className="d-flex nav-bar-right">
              <li className="nav-item pt-2 pe-4 nav-item-sign-in">
                <Link className="nav-link" to="/signin">
                  Sign in
                </Link>
              </li>
              <li className="nav-item pt-2 get-kuda-btn nav-item-get-kuda">
                <Link className="nav-link text-white" to="/getkuda">
                  Get Kuda
                </Link>
              </li>
              <li className="nav-item ps-4 nav-item-flag">
                <Link className="nav-link" to="/getkuda">
                  <img
                    src="data:image/svg+xml;base64,"
                    alt=""
                    className="pb-3"
                  />
                </Link>
              </li>
            </div>
          </ul>
        </div>
      </div>
    </nav>
  );
}
export default Navbar;
